import React from 'react';

export enum ButtonVariant {
    default,
    primary,
    secondary,
    success,
    info,
    danger,
    warning,
}

export enum ButtonSize {
    small,
    regular,
    large,
}

export interface IProps {
    outlined?: boolean;
    onClick?: () => void;
    disabled?: boolean;
    label: string,
    variant: ButtonVariant
    size: ButtonSize
}

export const Button: React.FC<IProps> = ({ 
    label,
    outlined = false,
    size = ButtonSize.regular,
    variant = ButtonVariant.default,
    ...props
 }) => {
    return (
        <button className='bg-blueGray-500 text-white active:bg-blueGray-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150'>
            {label}
        </button>
    )
}